/* HEADER */

.header_wrapper {
    position: relative;
    width: 1400px;
    margin: auto;
    overflow: hidden;
}
.header {
    display: flex;
    height: calc(100vh - 100px);
    align-items: center;
}
/* MAIN */

.main_img {
    height: 545px;
    width: 100%;
    display: block;
    transition: transform 0.1s;
    will-change: transform;
    background: url('./../../img/bg/main_bg.png') center/contain no-repeat;
    transform: translate3d(calc(200px + var(--mx)/25), calc(-100px + var(--my)/25), 0);
    z-index: 5;
    position: absolute;
}
.main_img:hover{
}

.transition-fade {
    transition: 0.4s;
    opacity: 1;
  }
  
html.is-animating .transition-fade {
    opacity: 0;
}
.bg_img {
    width: 800px;
    position: absolute;
    z-index: 100;
    top: 0;

}
.main_bg {
    background: #F5FBFC;
    opacity: 0.87;
    position: absolute;
    top: -10%;
    right: -10%;
    width: 105%;
    height: 100%;
    z-index: -1;
    transform: skew(18deg, 5deg);
}

