/* GENERAL STYLES */

@import url(https://fonts.googleapis.com/css?family=Inter:100,200,300,regular,500,600,700,800,900);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter';
    color: rgb(45, 45, 45);
}

html{  font-size: 15px; }

.site_header { }

.ads {    background: #35B8BE; }

a{text-decoration: none;}

/* CONTAINER */

.container {
    width: 1200px;
    margin: auto;
}
.main_text {
    width: 50%;
}
.title {
    font-style: normal;
    font-weight: 700;
    font-size: 60px;
    line-height: 60px;
    /* or 100% */

    letter-spacing: 1.8px;
    margin-bottom: 27px;
}
.blue_text{
    color: #35B8BE;
}
.subtitle {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    /* or 134% */

    letter-spacing: 0.36px; 
}

.click_order {  }
.star_description {}
.star {}
.description {}


/* ABOUT */
.about_title{
    text-align: center;
    font-size: 2.5em;
    font-weight: 500;
}
.about_description{
    margin-top: 65px;
    font-size: 2em;
    border: 3px dashed rgba(128, 187, 255, 0.484);
    padding: 15px 10px;    
}
.about_description_for{
    margin-top: 25px;
    font-size: 2em;
}
.about_description_list{
    list-style-type: none;
}
.about_description_list li{
    font-size: 1.5em;
    margin-top: 10px;
    margin-left: 15px;
}
.about_description_list li::before{
    content: '...  ';

}




/* ORDER */

.order_wrapper{
    margin-top: 50px;
}

.sub_title{
    text-align: center;
    font-size: 2.5em;
    font-weight: 500;
}
.order_form{
    margin: 50px auto;
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 50%;

}

.order_form>input{
    width: 100%;
    height: 40px;
    padding: 10px 30px;
    font-size: 1.5em;
    border-radius: 12px;
    border: 1px solid #4a8d9871 ;
    outline: none;
    background: #fff;
}
.order_form>input:last-child{
    color: rgba(0, 0, 0, 0.575);
    height: auto;
    background: #7ff01d35;
    cursor: pointer;
}

.order_form>input::placeholder{
    color: rgba(0, 0, 0, 0.291)
}
.order_form>input:focus{
    border: 1px solid rgb(23, 215, 110);
}
.order_form>textarea::placeholder{
    color: rgba(0, 0, 0, 0.291)
}
.order_form>textarea{
    padding: 10px 25px;
    height: 150px;
    font-size: 1.5em;
    border-radius: 12px;
    border: 1px solid #4a8d9871 ;
}

/* BUTTONS */

.btn{
    display: block;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 20px;
    /* identical to box height, or 118% */
    
    text-align: center;
    padding: 20px 35px;
    background: #35B8BE;
    border-radius: 6px;
    border: none;
    color: #FFFFFF;
    margin: 30px 0 0;
    
    cursor: pointer;
    width: fit-content;
}



/* FOOTER */

.site_footer {
    background: #242424ab;
    height: 200px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.copyright{
    color: white;
    font-size: 1.5rem;
}