/* MENU */

.main_menu {
    display: flex;
    padding: 0;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.logo {
    display: block;
    width: 100px;
    cursor: pointer;
    font-size: 3em;
    position: relative;
    transition: 0.5s;
}

.logo:hover{
    color: #35B8BE;
}
.fa-star{
    color: gold;
}

.logo:hover>.logo_word_1,
.logo:hover>.logo_word_2,
.logo:hover>.logo_word_3{
    font-size: 1em;
    opacity: 1;
}
.logo:hover>.logo_word_1{ top: 1em; }
.logo:hover>.logo_word_2{ top: 2em; }
.logo:hover>.logo_word_3{ top: 3em; }


.logo_word_1,
.logo_word_2,
.logo_word_3{
    position: absolute;  
    display: block;
    top: 0;
    opacity: 0;
    transition:  0.5s;
    color: #00000098;
    pointer-events: none
}
.logo_word_1{ left: 0; }
.logo_word_2{ left: 0.75em; }
.logo_word_3{ left: 1.5em; }


.menu {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    padding: 40px 0;
}
.menu_item {
    margin-left: 32px;
}
.menu_item a{
    transition: 0.2s;
    font-style: normal;
    font-weight: 400;
    font-size: 1.2em;
    line-height: 20px;
    color: #28224B;
    text-decoration: none;
    display: inline-block;
}
.menu_item a:hover{
    color: #35B8BE;
    transform: scale(1.08);
}
